import React from "react"

const Header = ({ tagline }) => {
    return (
        <header className="top">
            <h1>
                Deal
                <span className="ofThe">
                    <span className="of">of</span>
                    <span className="the">the</span>
                </span>
                Day
            </h1>
            <h3 className="tagline">
                <span>{tagline}</span>
            </h3>
        </header>
    )
}

export default Header



